/* FILTRI */
.nfts-filters {
    position: sticky;
    top: 0;
    background-color: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    margin-bottom: 20px;
    padding: 0 10px 0 10px
}
.nfts-filters-filters {
  display: flex;
  height: 60px;
  justify-content: space-between;
  align-items: center;
}

.nfts-filters-container {
  height: auto;
  max-height: 0;

  display: flex;
  flex-wrap: wrap;

  overflow: hidden;

  transition: max-height 0.3s ease-in-out;

  border-radius: 25px;
}
.nfts-filters-container-active {
  max-height: 400px;
}
.nfts-filter-item {
  width: calc(50% - 80px);
  margin: 20px 40px;
}
.nfts-filter-item-accept {
  width: calc(100% - 40px);
  padding: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.nfts-filter-title {
  margin-top: 12px;
  margin-bottom: 10px;
  color: var(--white);
}
.nfts-filter-filter {
}
.nfts-skillz {
  padding: 5px 10px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 40px;
  margin: 5px;
  color: var(--white);

  cursor: pointer;
  user-select: none;

  transition: background-color 0.3s ease-in-out;
}
.nfts-tags {
  display: flex;
  width: calc(50% - 50px);
  overflow-x: scroll;
  mask-image: linear-gradient(90deg, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%);

  padding-right: 50px;
}

.nfts-tags::-webkit-scrollbar {
    display: none;
}

.nfts-tag {
  padding: 5px 10px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  margin: 5px;
  color: var(--white);

  cursor: pointer;
  user-select: none;
}

.nfts-filter {
  display: flex;
  align-items: center;
}

.nfts-sort {
  position: relative;
  padding: 5px 10px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 40px;
  margin: 5px;
  color: var(--white);

  cursor: pointer;

  user-select: none;
  transition: background-color 0.3s ease-in-out;
}
.nfts-sort-list {
  position: absolute;
  max-height: 0;
  height: auto;
  width: auto;

  background-color: rgba(220, 119, 38, 0.9);

  right: 0px;
  top: calc(100% + 12px);

  border-radius: 12px;

  overflow: hidden;

  transition: max-height 0.3s ease-in-out;

  z-index: 3;
}

.nfts-sort-list-active {
  max-height: 400px;
}

.nfts-tag-active {
    background-color: rgba(220, 119, 38, 0.9);
}

/* NFTS */

.nfts-items {
  margin-top: 0px;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-wrap: wrap;
}

.nfts-items-loader {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;
  pointer-events: none;

}
.nfts-items-nothing {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  user-select: none;
  pointer-events: none;

}

.nfts-item {
  border-radius: 25px;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.5);

  cursor: pointer;

  box-shadow: 0 1px 1px hsl(0deg 0% 0% / 0.075),
    0 2px 2px hsl(0deg 0% 0% / 0.075), 0 4px 4px hsl(0deg 0% 0% / 0.075),
    0 8px 8px hsl(0deg 0% 0% / 0.075), 0 16px 16px hsl(0deg 0% 0% / 0.075);
}
.nfts-image {
  width: 100%;
  padding-bottom: 100%;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.nfts-description {
  padding: 0 15px 12px;
  pointer-events: none;
  user-select: none;

  color: white;
}
.nfts-description-subtitle {
  margin-top: 12px;
  height: 30px;
  font-size: 13px;
}
.nfts-description-title {
  margin-top: 5px;
  font-size: 17px;
  line-height: 26px;
  font-weight: 700;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  width: 100%;
}
.nfts-description-prices {
  box-sizing: border-box;
  margin: 0px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 5px;
}
.nfts-description-price {
  box-sizing: border-box;
  margin: 0px;
  flex-direction: column;
  -webkit-box-align: flex-start;
  align-items: flex-start;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
}
.nfts-description-price-title {
  box-sizing: border-box;
  margin: 0px;
  font-size: 10px;
  font-weight: 600;
  line-height: 1.3em;
  background-repeat: no-repeat;
  background-size: cover;
}
.nfts-description-price-price {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
}
.nfts-description-price-price-currencies {
  margin-right: 5px;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.nfts-description-price-price-price {
  margin: 0px;
  line-height: 1.3em;
  color: white;
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 18px;
  font-weight: bold;
}

.nfts-description-footer {
  margin-top: 15px;
  pointer-events: fill;
  user-select: text;
  cursor: pointer;
  font-size: 14px;
}

.nfts-item-container {
  width: calc(25% - 20px);
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 25px;
}

.details-panel {
  position: absolute;

  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  background-color: blue;

  z-index: 10;
}

@media (max-width: 1000px) {
  .nfts-item-container {
    width: calc(33.3333% - 20px);
  }
}
@media (max-width: 700px) {
  .nfts-item-container {
    width: calc(50% - 20px);
  }
}
@media (max-width: 600px) {
  .nfts-item-container {
    width: calc(100% - 20px);
  }
}
.top-sellers-title {
  color: var(--white);
  text-align: left;
  margin-bottom: 30px;
}

.top-sellers-item {
  width: 100px;
  height: 120px;
}

.top-sellers-image {
  width: 100px;
  height: 100px;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  border-radius: 100px;

  overflow: hidden;
}

.top-sellers-name {
  color: var(--white);
}

/* NFT Viewer */

.nft-viewer-main {
  position: absolute;
  width: 100vw;
  height: calc(100vh - 60px);

  display: flex;
  flex-direction: column;

  z-index: 99;

  backdrop-filter: blur(10px);
}

.area-personale-menu {
  position: relative;

  background-color: rgba(0, 0, 0, 0.2);

  height: calc(100% - 20px);
  width: calc(25% - 20px);

  padding: 10px;

  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
}

.nft-viewer-info-one {
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);

  height: calc(100% - 20px);

  width: calc(40% - 50px);

  padding: 10px 10px 10px 40px;
}

.nft-viewer-info-two {
  position: relative;

  background-color: rgba(0, 0, 0, 0.5);

  color: var(--white);

  height: calc(100% - 20px);

  width: calc(60% - 50px);

  padding: 10px 10px 10px 40px;
}

.nft-viewer-nav {
  height: 40px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.nft-viewer-page {
  display: flex;
  height: calc(100% - 40px);

  overflow-y: scroll;
}

.nft-viewer-close-button {
  width: 40px;
  height: 40px;
  text-align: center;

  cursor: pointer;

  margin-right: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.nft-viewer-image {
    width: 100%;
    height: 0;
    padding-bottom: 100%;

    background-position: center;
    background-size: cover;

    overflow: hidden;

    border-radius: 12px;
}