.App {
  text-align: center;
  overflow: hidden;
}

.site-container {
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  height: 100vh;
  width: 100vw;

  z-index: 1;

  overflow-x: hidden;
}

.sfondo-video-home-container {
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  height: 100vh;
  width: 100vw;

  z-index: 0;

  overflow-x: hidden;
}

.sfondo-video-home-container video {
  height: 100%;
  width: 100%;

  object-fit: cover;
}

.sfondo-video-home {
  position: absolute;

  height: 100%;

  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);
}

/* SLIDER */

.slider-container {
  width: 100vw;
  height: calc(100vh - 60px);

  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper {
  min-height: 300px;
}

.swiper-pagination-bullet-active {
  background: var(--orange) !important;
}

.card-home {
  padding: 30px;

  backdrop-filter: blur(10px);

  border-radius: 15px;

  color: white;

  max-width: 450px;

  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);

  cursor: pointer;

  user-select: none;

  margin: 20px auto;
}
.card-home-container {
  padding: 15px;
}

.card-home h1 {
  font-size: 60px;
  margin: 0;
  padding: 0;
}

/* Loader */

.loader-main {
  position: absolute;
  width: 100vw;
  height: 100vh;

  top: 0;
  z-index: 999;

  background-color: black;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.animator {
  width: 100px;
}

/* SHARE PANEL */
.rws-backdrop {
  backdrop-filter: blur(12px);
}
.rws-container {
  background-color: rgba(0, 0, 0, 0.3) !important;
}
.rws-header {
  color: var(--white);
}
.rws-icons {
  grid-template-columns: repeat(5, 1fr) !important;
  grid-gap: 1rem !important;
}
.rws-icon {
  border-radius: 100% !important;
}
.rws-close {
  margin: 20px !important;
  width: calc(100% - 40px) !important;
  border-radius: 12px !important;
  color: var(--white) !important;
  background-color: rgba(0, 0, 0, 0.4) !important;
}
.terms-and-conditions {
  position: absolute;
  z-index: 9;
  bottom: 0;
  left: 0;

  display: flex;
  padding: 10px;

  color: rgb(255 255 255 / 30%);

  font-size: 12px;
}
.terms-and-conditions > div {
  padding: 5px;
}
.terms-and-conditions-links {
  display: flex;
}
.terms-and-conditions-links > div {
  margin: 0 10px;
  cursor: pointer;
}
.terms-and-conditions-links > div:hover {
  color: var(--orange);
}
@media (max-width: 800px) {
  .card-home h1 {
    font-size: 40px;
  }
  .swiper {
    min-height: 350px;
  }
  .slider-container {
    height: 100%;
  }
}
