.profile-container {
    position: relative;
    background-color: aqua;

    width: 100vw;
    height: 100vh;

    overflow: hidden;

    display: flex;

    z-index: 1;
}

.profile-menu {
    background-color: blue;

    width: 30vw;
    height: 100vh;

    overflow: hidden;
}

.profile-info {
    background-color: yellow;

    width: 70vw;
    height: 100vh;

    overflow: hidden;
}