.overview-main {
  position: absolute;
  width: 100vw;
  height: calc(100vh - 60px);

  display: flex;
  flex-direction: column;

  z-index: 90;

  backdrop-filter: blur(10px);
}

.overview-page {
  display: flex;
  height: 100%;
}

.overview-menu {
  position: relative;

  background-color: rgba(0, 0, 0, 0.2);

  height: calc(100% - 20px);
  width: calc(30% - 40px);

  padding: 10px 20px;

  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);

  overflow-y: scroll;
}
.overview-menu::-webkit-scrollbar {
  display: none;
}

.overview-title {
  display: flex;
  margin: 30px 0px 5px 0px;
}

.overview-info {
  position: relative;

  background-color: rgba(0, 0, 0, 0);

  height: calc(100% - 20px);

  width: calc(70% - 20px);

  padding: 10px 20px;

  backdrop-filter: blur(10px);

  overflow-y: scroll;
}

.overview-info::-webkit-scrollbar {
  display: none;
}

.overview-list {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  flex-flow: row wrap;
  justify-content: center;
}

.overview-list-item {
  padding: 6px 12px;
  margin: 6px;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 100px;
}
.overview-link-item {
  padding: 6px 12px;
  margin: 6px;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 100px;
  cursor: pointer;

  transition: background-color 0.3s ease-in-out;
}

.overview-link-item:hover {
  background-color: var(--orange);
}

table {
  width: 100%;
}
table,
th,
td {
  border: 1px solid var(--white);
  border-collapse: collapse;
}

th,
td {
  padding: 10px;
}

.overview-share {
  cursor: pointer;
}

.overview-button-top {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.2);

  border-radius: 12px;

  padding: 10px;

  position: sticky;
  top: 0px;
}
.overview-menu-trigger, .closed-overview-menu {
  display: none;
}
@media (max-width: 800px) {
  .personal-area-menu-trigger {
    display: block;
  }
  .overview-menu {
    position: absolute;

    height: calc(100% - 20px);
    width: calc(100% - 20px);

    padding: 10px;

    z-index: 11;

    background-color: rgba(0, 0, 0, 0.9);

    left: -100%;

    transition: left 0.3s ease-in-out;
  }

  .overview-menu-active {
    left: 0;
  }

  .overview-nav {
    justify-content: space-between;
  }

  .overview-info {
    position: relative;

    background-color: rgba(0, 0, 0, 0);

    height: calc(100% - 20px);

    width: calc(100% - 20px);

    padding: 10px;

    backdrop-filter: blur(10px);

    z-index: 10;
  }

  .overview-page {
    height: 100%;
  }

  .overview-menu-trigger, .closed-overview-menu {
    display: block;
  }

  .overview-share {
    position: relative;
    top: inherit;
    right: inherit;
  }

  .overview-menu-trigger {
    display: block;
  }
  .overview-button-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  
}
