.ui-viewer-main {
  position: relative;

  width: calc(100vw - 60px);
  height: calc(100vh - 120px);

  padding: 30px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  flex-flow: row wrap;
  justify-content: center;

  backdrop-filter: blur(12px);
}

.foody-button {
  padding: 5px 10px;
  border-radius: 12px;
  margin: 5px;
  cursor: pointer;
  user-select: none;

  transition: 0.3s ease-in-out;

  text-align: center;

  height: fit-content;

  font-size: 15px;

  border: none;
}
.foody-input-container {
  position: relative;
  height: fit-content;
}
.foody-input {
  padding: 5px 10px;
  border-radius: 12px;
  margin: 5px;

  user-select: none;
  border: none;
  outline: none;
  transition: 0.3s ease-in-out;

  height: fit-content;
  font-size: 15px;
}
.foody-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.foody-input-icon {
  position: absolute;
  height: 100%;
  width: 39px;
  top: 50%;

  border-radius: 100%;

  background-color: var(--orange);

  display: flex;
  justify-content: center;
  align-items: center;

  transform: translateY(-50%);

  transition: 0.3s ease-in-out;
}