.nav-pre-container {
  position: relative;
  height: 60px;
  width: 100vw;
  backdrop-filter: blur(10px);
  z-index: 80;
}
.nav-container {
  
  z-index: 60;
  height: 40px;
  width: calc(100% - 20px);

  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.2);
  color: white;

  

  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);

  transition: all 0.3s ease-in-out;

  opacity: 0.1;

  
}

.nav-container:hover {
  opacity: 1;
}

/* LOGO DESIGN */

.logo-main {
  position: absolute;

  width: 40px;
  height: 40px;

  left: 0;
}

.logo-container {
  position: absolute;

  top: 0;
  left: 0;

  width: 80px;
  height: 80px;

  cursor: pointer;
}

/* Menu Links */

.menu-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-link {
  padding: 10px 10px;
  margin: 0 20px;
  cursor: pointer;
}

.menu-link:hover {
  color: var(--orange);
}

.mobile-menu {
  display: none;
}

.login-container {
  position: absolute;
  right: 10px;
}

.mobile-menu-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  z-index: 999;
  background-color: black;

  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.menu-link-mobile {
  padding: 20px;
  font-size: 30px;
  color: var(--white);
}

@media (max-width: 800px) {
  .menu-link {
    display: none;
  }
  .mobile-menu {
    display: block;
  }
  .logo-container {
    width: 40px;
    height: 40px;
  }
  .nav-container {
    opacity: 1;
    justify-content: space-between;
    
  }
  .logo-main {
    left: 50%;
    transform: translate(-50%);
  }
  .login-container {
    position: relative;
  }

}
