@import url("https://fonts.googleapis.com/css2?family=Averia+Sans+Libre:wght@300;400;700&family=Dancing+Script:wght@400;700&family=Open+Sans:wght@400;600;700&family=Roboto:wght@400;700&family=Work+Sans:ital,wght@0,100;0,500;0,600;0,700;0,900;1,400&display=swap");
@import url("./Style/base.css");
@import url("./Style/nav.css");
@import url("./Style/profile.css");
@import url("./Style/personal-area.css");
@import url("./Style/marketplace.css");
