.personal-area-main {
  position: absolute;
  width: 100vw;
  height: calc(100vh - 60px);

  display: flex;
  flex-direction: column;

  z-index: 99;

  backdrop-filter: blur(10px);
}

.personal-area-menu {
  position: relative;

  background-color: rgba(0, 0, 0, 0.2);

  height: calc(100% - 20px);
  width: calc(30% - 20px);

  padding: 10px;

  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
}

.personal-area-info {
  position: relative;
  background-color: aqua;

  background-color: rgba(0, 0, 0, 0);

  height: calc(100% - 20px);

  width: calc(70% - 20px);

  padding: 10px;

  backdrop-filter: blur(10px);

  overflow-y: scroll;
}

.personal-area-nav {
  height: 40px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.personal-area-page {
  display: flex;
  height: calc(100% - 40px);
}

.close-button {
  width: 40px;
  height: 40px;
  text-align: center;

  cursor: pointer;

  margin-right: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.open-menu-button {
  width: 40px;
  height: 40px;
  text-align: center;

  cursor: pointer;

  margin-right: 10px;

  display: none;
  align-items: center;
  justify-content: center;
}

.personal-area-info-utente {
  width: calc(100% - 20px);
  min-height: 100px;
  padding: 10px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.personal-area-foto-e-nome {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.personal-area-nome-utente {
  font-size: 20px;
  color: var(--white);
  margin: 0 0 10px 0;

  user-select: none;
}

.personal-area-adress {
  color: var(--white);
  cursor: pointer;

  transition: color 0.3s ease-in-out;

  user-select: none;
}

.personal-area-adress:hover {
  color: var(--orange);
}

.personal-area-nome-e-adress {
  margin: 0 0 0 10px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.personal-area-avatar {
  width: 100px;
  height: 100px;
  border-radius: 100px;
  overflow: hidden;
}
.avatars-list {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  flex-flow: row wrap;
}
.avatar-container {
  width: 80px;
  height: 80px;
  margin: 10px;
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s ease-in-out;
}

.avatar-container:hover {
  transform: scale(1.1);
  margin-top: 5px;
  background-color: rgba(0, 0, 0, 0.4);
}

.avatar-selected {
  background-color: var(--orange);
}
.avatar-selected:hover {
  background-color: var(--orange);
}
.personal-area-menu-trigger {
  display: none;
}

.personal-area-data-item {
  padding: 10px 20px;
  margin: 6px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 100px;
  color: var(--white);
  text-align: left;

  display: flex;
  align-items: center;
  justify-content: space-between;
}
.personal-area-data-item h4, .personal-area-data-item p {
  margin: 0;
}
.personal-area-data-editor {
  display: flex;
  align-items: center;
}

.personal-area-data-single {
  margin: 1px;
}

@media (max-width: 800px) {
  .personal-area-menu-trigger {
    display: block;
  }
  .open-menu-button {
    display: flex;
  }
  .personal-area-data-editor-opened {
    display: none;
  }
  .personal-area-data-item-opened {
    justify-content: flex-end;
  }
  .personal-area-menu {
    position: absolute;

    height: calc(100% - 60px);
    width: calc(100% - 20px);

    padding: 10px;

    z-index: 11;

    background-color: rgba(0, 0, 0, 0.9);

    top: 40px;
    left: -100%;

    transition: left 0.3s ease-in-out;
  }

  .personal-area-menu-active {
    left: 0;
  }

  .personal-area-nav {
    justify-content: space-between;
  }

  .personal-area-info {
    position: relative;

    background-color: rgba(0, 0, 0, 0.2);

    height: calc(100% - 20px);

    width: calc(100% - 20px);

    padding: 10px;

    backdrop-filter: blur(10px);

    z-index: 10;

    text-align: center;
  }
  .avatars-list {
    justify-content: center;
  }
  .avatar-container:hover {
    transform: scale(1);
    margin-top: 10px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .avatar-selected:hover {
    background-color: var(--orange);
  }
}
