.marketplace-main {
    position: absolute;
    width: 100vw;
    height: calc(100vh - 60px);

    display: flex;
    flex-direction: column;

    z-index: 2;

    transition: left 0.4s ease-in-out;

    backdrop-filter: blur(10px);
}


.marketplace-menu {
    position: relative;
    
    background-color: rgba(0,0,0,0.2);

    height: calc(100% - 20px);
    width: calc(30% - 20px);

    padding: 10px;

    box-shadow: 0px 5px 5px rgba(0,0,0,0.2);

    border-top-right-radius: 12px;

    overflow-y: scroll;

}

.marketplace-nfts {
    position: relative;

    background-color: rgba(0,0,0,0);

    height: calc(100% - 10px);

    width: calc(70% - 20px);

    padding: 0 10px 10px 10px;

    backdrop-filter: blur(10px);

    overflow-y: scroll;

}

.marketplace-nfts::-webkit-scrollbar {
    display: none;
}

.marketplace-nav {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.marketplace-page {
    display: flex;
    height: calc(100% - 10px);
    margin-top: 10px;
}

.marketplace-menu-tags {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    flex-flow: row wrap;
    justify-content: center;
}

.marketplace-menu-title {
    margin-top: 20px;
    margin-bottom: 15px;
    color: var(--white);

    font-size: 16px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.marketplace-menu-remove-all-tags {
    font-size: 12px;
    cursor: pointer;
    user-select: none;
}

.marketplace-nft-load-more {
    background-color: rgba(0,0,0,0.2);

    width: calc(100% - 20px);

    margin: 0 10px 40px 10px;

    text-align: center;
    color: var(--white);

    cursor: pointer;

    user-select: none;
    padding: 10px 0;

    border-radius: 12px;
}

.nfts-sort-filter {
  display: none;
  cursor: pointer;
}

.flag-avatar {
  border-radius: 2px;
  width: 20px;
  height: 15px;
  overflow: hidden;
  margin-right: 10px;

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.world-avatar {
  border-radius: 2px;
  width: 40px;
  height: 40px;
  display: none;

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 800px) {
    .personal-area-menu-trigger {
      display: block;
    }
    .marketplace-menu {
      position: absolute;
  
      height: calc(100% - 20px);
      width: calc(100% - 20px);
  
      padding: 10px;
  
      z-index: 11;
  
      background-color: rgba(0, 0, 0, 0.9);
  
      left: -100%;
  
      transition: left 0.3s ease-in-out;

      border-top-right-radius: 0px;
      top: 0;
    }
  
    .marketplace-menu-active {
      left: 0;
    }
  
    .marketplace-nav {
      justify-content: space-between;
    }
  
    .marketplace-nfts {
      position: relative;
  
      background-color: rgba(0, 0, 0, 0);
  
      height: calc(100% - 20px);
  
      width: calc(100% - 20px);
  
      padding: 10px;
  
      backdrop-filter: blur(10px);
  
      z-index: 10;
    }
  
    .marketplace-page {
      height: calc(100% - 10px);
    }
  
    .marketplace-menu-trigger, .closed-marketplace-menu {
      display: block;
    }
  
    .marketplace-share {
      position: relative;
      top: inherit;
      right: inherit;
    }
  
    .marketplace-menu-trigger {
      display: block;
    }
    .marketplace-button-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .nfts-tags {
      display: none !important;
    }
    .nfts-sort-filter {
      display: block;
    }
  }